@media screen and (max-width: 1400px), screen and (max-device-width: 1400px) {
  body {
    .content {
      .container {
        grid-template-columns: 25% 1fr 20%;
        .content__popularProducts {
          ul {
            li {
              width: 230px;
              height: 290px;
            }
            li {
              img {
                width: 150px;
                height: 150px;
              }
            }
          }
        }
      }
    }
  }
}
